<template>
  <div class="stepOut">
    <ul>
      <li class="stepItem" v-for="(stepItem, index) in stepInfo.list" :key="index">
        <div :class="stepInfo.step>=index+1 ? 'icon active' : 'icon'"></div>
        <div :class="stepInfo.step>=index+2 ? 'line lineActive' : 'line'" v-show="index!==stepInfo.list.length-1"></div>
        <p class="stepStatus">{{stepItem.status}}</p>
        <p class="statusTime">{{stepItem.statusTime}}</p>
      </li>
    </ul>
  </div>
</template>

<style lang="less" scoped>
  .stepOut {
    width: 100%;
    height: 70px;
    display: flex;
    //justify-content: center;
    ul {
      padding: 0;
      margin: 0;
    }

    li {
      list-style: none;
    }

    .stepItem {
      width: 120px;
      height: 70px;
      float: left;
      font-family: SimSun;
      font-size: 16px;
      text-align: center;
      position: relative;

      .icon {
        width: 21px;
        height: 21px;
        border-radius: 50%;
        border:5px solid rgba(221,221,221,0.8);
        background: white;
        margin: 0 auto;
        position: relative;
        z-index: 888;
      }

      .active {
        border-color: #0B897E;
      }

      .line {
        position: absolute;
        top: 8px;
        left: 65%;
        border-bottom: 4px dashed #ddd;
        width: 80px;
        z-index: 111;
      }

      .lineActive {
        border-bottom: 4px dashed #0B897E;
      }

      .stepStatus {
        font-family: PingFangSC-Medium, PingFang SC;
        font-size:14px;
        font-weight: bold;
        color: rgba(51, 51, 51, 1);
        line-height: 27px;
        margin-top: 5.67px;
      }

      .statusTime {
        color: rgba(87, 87, 87, 1);
        opacity: 0.5;
      }
    }
  }
</style>

<script>
  export default {
    name: 'Steps',
    props: {
      stepInfo: {
        type: Object,
        default: function () {
          return {
            list: [],
            step: 0
          }
        }
      }
    }
  }
</script>
